.about_me {
  margin: 0 auto;
  padding: 20px;
  text-align: justify;
  /* border: 1px solid #ddd; */
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.about_me h2 {
  font-size: 1.5rem;
  color: white;
}

.about_description {
  font-size: 1.2rem;
  font-weight: 300;
  color: white;
  text-align: justify;
  word-spacing: normal;
}
.pagination {
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  gap: 5px;
}
